body {
  margin: 0;
  padding: 0;
  font-family: 'Titillium Web', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  display: flex;
  flex-direction: column;

  
  text-align: left;
}

.header{
  font-size: 40px;
  font-weight: 400;
  margin: 40px;

  margin-left: 5%;
}

.input{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  width: 50%;

  margin-left: 5%;
}

.input_label{
  font-size: 20px;
  font-weight: 400;
}

.input input{
  margin: 10px;
  width: 450px;
  height: 30px;

  border-radius: 10px;


  font-size: 16px;
  font-weight: 300;
}

.input button{
  border: 1px solid;
  border-radius: 10px;

  height: 30px;
  width: 60px;

  font-size: 15px;
  font-weight: 400;

  cursor: pointer;
}

.lock_number{
  margin: 50px;

  font-size: 30px;
  font-weight: 700;

  margin-left: 5%;
}

.tx_table{
  margin-left: 5%;
  font-size: 15px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

